// 
import Vrvideo from './Vr_video/index'
import Vrmodel from './Vr_model/index'

//  
const components = [ Vrvideo, Vrmodel,]


//  install  Vue  use  
const install = function (Vue) {
	components.forEach(component => { 
		Vue.component(component.name, component) 
	}) 
}

// , Vue.use() 
if (typeof window !== 'undefined' && window.Vue) { 
	install(window.Vue)
}

//  install Vue.use()  
export default { 
	install,
    // playVr,
    // modelVr
}
